}
html {
  scroll-behavior: smooth;
}
body {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-family: "Roboto", sans-serif;
  color: #333;
}
textarea {
  width: 100%;
}
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}
.wrapper {
  padding: 1rem;
  overflow: hidden;
}
.content {
  overflow: hidden;
}
.separator {
  margin: 0;
  height: 1px;
  margin-top: 1rem;
  box-shadow: 0px 0px 10px 20px
    radial-gradient(50% 20rem, rgba(0, 105, 255, 0.7) 5%, rgba(0, 0, 0, 0) 100%);
  background: radial-gradient(
    50% 30rem,
    rgba(0, 105, 255, 0.7) 5%,
    rgba(0, 0, 0, 0) 100%
  );
  position: relative;
  top: 0;
  left: -100%;
  width: 100%;
  -webkit-animation: grad-rotate 5s linear infinite;
  animation: grad-rotate 5s linear infinite;
}
@-webkit-keyframes grad-rotate {
  from {
    left: -100%;
  }
  to {
    left: 100%;
  }
}
@keyframes grad-rotate {
  from {
    left: -100%;
  }
  to {
    left: 100%;
  }
}
.modal {
  display: block;
  position: fixed;
  z-index: 1001;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #1e90ff;
  background-color: rgba(0, 0, 0, 0.4);
  text-align: center;
}
.modal-content {
  position: relative;
  background-color: #fefefe;
  margin: 15% auto;
  padding: 1rem;
  border: 1px solid #888;
  width: 50%;
  max-height: 500px;
  overflow: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-animation-name: animatetop;
  animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-duration: 0.4s;
}
.modal-header {
  padding: 1rem;
  background-color: #04659e;
  border-radius: 5px;
  color: #fff;
}
.modal-body {
  padding: 4rem 1rem 4rem 1rem;
}
.modal-footer {
  padding: 1rem;
  background-color: #5cb85c;
  border-radius: 5px;
  color: #fff;
}
.close {
  pointer-events: stroke;
  color: #aaa;
  float: right;
  position: relative;
  top: -0.5rem;
  padding-bottom: 2rem;
  font-size: 2rem;
  font-weight: bold;
}
.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
