    top: 0;
  }
  header .menu__nav .nav__li .dropdown-btn {
    top: 0;
  }
}
header .menu__nav .nav__li .dropdown-container {
  display: none;
  border-radius: 0.25rem;
  background: #0693e3;
  box-shadow: 3px 5px 8px -3px #04659e;
  position: absolute;
  top: 100%;
  left: 30%;
  z-index: 1550;
  padding-right: 1rem;
  text-align: left;
  transition: all 300ms ease 0s;
}
header .menu__nav .nav__li .dropdown-container .dropdown__item {
  border-radius: 0.25rem;
  box-shadow: 0px 3px 5px 3px #04659e;
  margin: 1rem 0.5rem 1rem;
  min-width: 10rem;
}
