  width: 100%;
  padding: 0.5rem;
}
@media (max-width: 926px) {
  header .menu__nav .nav__li {
    height: 55px;
  }
}
@media (max-width: 767px) {
  header .menu {
    padding: 1rem 0;
  }
  header .menu .menu__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    left: 2rem;
  }
  header .menu .menu__burger span {
    position: absolute;
    background-color: #f0f8ff;
    width: 100%;
    height: 2px;
    top: 10px;
    transition: all 500ms ease 0s;
  }
  header .menu .menu__burger::before,
  header .menu .menu__burger::after {
    content: "";
    background-color: #f0f8ff;
    position: absolute;
    width: 100%;
    height: 2px;
    transition: all 300ms ease 0s;
  }
  header .menu .menu__burger::before {
    top: 0;
  }
  header .menu .menu__burger::after {
    top: 100%;
  }
  header .menu .menu__burger.active::before {
    transform: rotate(45deg);
    top: 0.6rem;
  }
  header .menu .menu__burger.active::after {
    transform: rotate(-45deg);
    top: 0.6rem;
  }
  header .menu .menu__burger.active span {
    transform: scale(0);
  }
  header .menu .nav {
    position: absolute;
  }
  header .menu .nav__ul {
    position: relative;
    top: 0.2rem;
    left: -110%;
    border-radius: 0.25rem;
    z-index: 100;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding-right: 0.2rem;
    transition: left 300ms ease 0s;
  }
  header .menu .nav__li {
    align-self: stretch;
    text-align: left;
    box-shadow: 0px 3px 10px -1px #04659e;
    margin: 0.5rem 0.1rem 0.5rem 0.1rem;
  }
  header .menu .nav__ul.active {
    left: -1px;
  }
