  content: " — ";
}
@media (max-width: 768px) {
  .review__grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "photo from" "message message";
  }
  .review__image {
    grid-area: photo;
  }
  .review__image img {
    min-width: 70%;
    min-height: 3rem;
    display: inline-block;
    border-radius: 50%;
    padding-top: 1rem;
    width: auto;
    height: auto;
  }
  .review__message {
    grid-area: message;
    text-align: left;
    font-style: italic;
    padding: 0.3rem;
  }
  .review__message p::before {
    content: '"';
  }
  .review__message p::after {
    content: '"';
  }
  .review__from {
    display: block;
    grid-area: from;
    text-align: left;
    margin: 0.5rem 0 0.5rem;
  }
  .review__from p {
    display: block;
    padding: 1rem 0.3rem 0.3rem 0.3rem;
    font-weight: 600;
  }
  .review__from p::before {
    content: " — ";
  }
}
